<template>
  <div class="">
    <h3>Reader</h3>
    <p>Random Number from backend: {{ randomNumber }}</p>
    <button class="btn btn-primary" @click="getRandom">New Random Number</button>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      randomNumber: 0
    }
  },
  methods: {
    getRandomInt (min, max) {
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min + 1)) + min
    },
    getRandom () {
      // this.randomNumber = this.getRandomInt(1, 100)
      this.randomNumber = this.getRandomNumberFromBackend()
    },
    getRandomNumberFromBackend () {
      const path = `[http://localhost:5000/api/random](http://localhost:5000/api/random)`
      axios.get(path).then(response => {
        this.randomNumber = response.data.randomNumber
      }).catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.getRandom()
  }
}
</script>

<style>
</style>



